.faq{
    margin-top: 96px;
    margin-bottom: 80px;
    @include pad{
        margin: 80px 0;
    }
    @include mobile{
        margin-top: 40px;
        margin-bottom: 64px;
    }
    .title{
        font-weight: 700;
        margin-bottom: 32px;
        border-left: 8px solid $primary;
        padding-left: 16px;
        @include mobile{
            margin-bottom: 24px;
        }
    }
}

h4{
    font-weight: 700;
    margin-bottom: 24px;
}

.qItems{
    .q{
        margin-bottom: 8px;
        font-weight: 700;
    }
    .answer{
        margin-bottom: 32px;
        @include mobile{
            margin-bottom: 24px;
        }
    }
}